.ImgBox{
    display: flex;
    align-items: center;
    justify-content:center;
    width: 130px;
    height: 130px;
    cursor: pointer;
    margin-left: 10px;
    transition: width 0.5s;
    transition: height 0.5s;
    img{
        width: auto;
        height: 100%;  
    }
  
}    
.ImgBox:hover{
    width: 150px;
    height: 150px;
}
.ImgBox.spin{
    animation: spinAnimation infinite 10s linear;
}
@keyframes spinAnimation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
